import { 
  Bar,
  BarChart,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
} from "recharts"; /* Nutzung der Extension Recharts zur Darstellung der Kosten in einem Diagramm */


function ResultDiagram(props) {

  /* Erstellen der gestapelten Balken */
  const data = [
    {
      Bezugsart: "Kauf",
      Arbeitsspeicher: props.data[0][0],
      Speicher: props.data[0][1],
    },
    {
      Bezugsart: "GreenLake",
      Arbeitsspeicher: props.data[1][0],
      Speicher: props.data[1][1],
    }
  ];

  return (
    <div className="resultDiagram">
      <BarChart width={400} height={500} data={data}>
        <XAxis dataKey="Bezugsart"></XAxis>
        <YAxis></YAxis>
        <Tooltip></Tooltip>
        <Legend></Legend>
        <Bar dataKey="Arbeitsspeicher" stackId="a" fill="rgb(139, 20, 20)"></Bar>
        <Bar dataKey="Speicher" stackId="a" fill="rgb(236, 125, 125)"></Bar>
      </BarChart>
    </div>
  );
}

export default ResultDiagram;