import Criterias from "./Criterias";
import ScoreBar from "./ScoreBar";
import ResultDiagram from "./ResultDiagram";
import CapacityCards from "./CapacityCards";

function Result(props) {

  /* Funktion zur Berechnung der Kostenwerte */
  const calculateResults = () => {
    let growthMonth = Math.pow((1 + props.growthRate * 0.01), 1/12) - 1; /* Umrechnung der jährlichen Wachstumsquote in die monatliche Wachstumsquote */
    let memory = props.capacities[0] * props.capacities[2];
    let storage = props.capacities[1] * props.capacities[2];
    let lifeCycle = props.capacities[3] * 12; /* Umrechung von Nutzungsdauer in Jahren zu Nutzungsdauer in Monaten */
    let costBuy = props.costHardware[0];
    let costGL = props.costHardware[1];
    let costMemoryBuy = 0;
    let costStorageBuy = 0;
    let costMemoryGL = 0;
    let costStorageGL = 0;
    let costSumBuy = 0;
    let costSumGL = 0;

    /* Hochrechnung der benötigten Kapazitäten über die Nutzungsdauer
    Kontinuierliche Erhöhung der Kostenwerte für GreenLake Bezug */
    for (let i = 0; i < lifeCycle; i++) {
      memory += memory * growthMonth;
      storage += storage * growthMonth;
      costMemoryGL += memory * (costGL[0]);
      costStorageGL += storage * (costGL[1]);
    }
    /* Berechnung der Kostenwerte für Bezug über Kauf mit Hilfe der benötigten Kapazitäten am Ende der Nutzungsdauer */
    costMemoryBuy = memory * costBuy[0];
    costStorageBuy = storage * costBuy[1];
    costSumBuy = costMemoryBuy + costStorageBuy;
    costSumGL = costMemoryGL + costStorageGL;

    return [
      [costMemoryBuy.toFixed(2), costStorageBuy.toFixed(2)],
      [costMemoryGL.toFixed(2), costStorageGL.toFixed(2)],
      [costSumBuy.toFixed(2), costSumGL.toFixed(2)],
    ];
  }

  let costs = calculateResults();


  return (
    <div>
      <div className="results">
        <div className="resultCriteria">
          <p className="resultTitle">Kriterien</p>
          <div className="resultCriteriaLabel">
            <p>Kriterium:</p>
            <p>Gewichtung:</p>
            <p>Eingabe:</p>
          </div>
          <Criterias
            callbackCriterias={props.callbackCriterias}
            criteriaValues={props.criteriaValues}
            tab={props.tab}
            weightings={props.weightings}
          />
          <ScoreBar
            criteriaValues={props.criteriaValues}
            weightings={props.weightings}
          />
        </div>
        <div className="resultCapacity">
          <p className="resultTitle">Kosten</p>
          <CapacityCards
            capacities={props.capacities}
            growthRate={props.growthRate}
            disabled={true}
          />
          <div className="resultCapacityCosts">
            <div className="resultCostsSums">
              <div className="resultCostsCard">
                <p>Gesamtkosten Kauf der Ressourcen:</p>
                <p className="resultCostAmount">{costs[2][0]}€</p>
              </div>
              <div className="resultCostsCard">
                <p>Gesamtkosten Bezug über HPE GreenLake:</p>
                <p className="resultCostAmount">{costs[2][1]}€</p>
              </div>
            </div>
            <ResultDiagram data={costs} /> {/* Übergabe der berechneten Kosten an das Diagramm */}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Result;