function CapacityCards(props) {


  return (
    /* Einzelne Karten zur Anzeige und Möglichkeit der Eingabe für die einzelnen Kapazitäten, Nutzungsdauer und Wachstumsquote */
    <div className={props.disabled ? "capacityInputFast result" : "capacityInputFast"}>
      <div className="capacityInputCard">
        <label htmlFor="memory">
          Durchschn. Arbeitsspeicher pro VM (in GB):
        </label>
        <input
          className={props.disabled ? "inputCapacityFast result" : "inputCapacityFast"}
          id="memory"
          type="number"
          min="0"
          value={props.capacities[0]}
          disabled={props.disabled ? true : false}
          onChange={(e) =>
            props.callbackCapacities(parseFloat(e.target.value), 0)
          }
        />
      </div>
      <div className="capacityInputCard">
        <label htmlFor="cpu">Durchschn. Anzahl an vCPUs pro VM:</label>
        <input
          className={props.disabled ? "inputCapacityFast result" : "inputCapacityFast"}
          id="cpu"
          type="number"
          min="0"
          value={(props.capacities[0] / 8) * 4}
          disabled
        />
      </div>
      <div className="capacityInputCard">
        <label htmlFor="storage">Durchschn. Speicher pro VM (in GB):</label>
        <input
          className={props.disabled ? "inputCapacityFast result" : "inputCapacityFast"}
          id="storage"
          type="number"
          min="0"
          value={parseFloat(props.capacities[1])}
          disabled={props.disabled ? true : false}
          onChange={(e) =>
            props.callbackCapacities(parseFloat(e.target.value), 1)
          }
        />
      </div>
      <div className="capacityInputCard">
        <label htmlFor="vm">Anzahl an VMs:</label>
        <input
          className={props.disabled ? "inputCapacityFast result" : "inputCapacityFast"}
          id="vm"
          type="number"
          min="0"
          value={props.capacities[2]}
          disabled={props.disabled ? true : false}
          onChange={(e) =>
            props.callbackCapacities(parseFloat(e.target.value), 2)
          }
        />
      </div>
      <div className="capacityInputCard">
        <label htmlFor="lifeCycle">geplante Nutzungsdauer (in Jahren):</label>
        <input
          className={props.disabled ? "inputCapacityFast result" : "inputCapacityFast"}
          type="number"
          min="1"
          id="lifeCycle"
          value={props.capacities[3]}
          disabled={props.disabled ? true : false}
          onChange={(e) =>
            props.callbackCapacities(parseFloat(e.target.value), 3)
          }
        />
      </div>
      <div className="capacityInputCard">
        <label htmlFor="growth">Wachstumsquote (in Prozent):</label>
        <input
          className={props.disabled ? "inputCapacityFast result" : "inputCapacityFast"}
          value={props.growthRate}
          min="0"
          id="growth"
          disabled={props.disabled ? true : false}
          onChange={(e) =>
            props.callbackgrowth(e.target.value, 1)
          }
        />
      </div>
    </div>
  );
}

export default CapacityCards;