import CapacityCards from "./CapacityCards";
import ReactHtmlParser from "../../node_modules/react-html-parser";
import { useEffect } from "react";


function InputCapacity(props) {
  /* Generierung der Tabelle mit den aktuell gespeicherten einzelnen Eingaben zu den VMs */
  let detailedTable =
    "<table><thead><tr><th>Arbeitsspeicher</th><th>Speicher</th><th>Anzahl</th></tr></thead><tbody>";

  for (let i = 0; i < props.capacityVMs.length; i++) {
    let current = props.capacityVMs[i];
    detailedTable += `<tr><td>${current["memory"]}</td><td>${current["storage"]}</td><td>${current["amount"]}</td></tr>`;
  }

  detailedTable += "</tbody></table>";
  detailedTable =
    ReactHtmlParser(detailedTable); /* string wird in HTML transformiert */
  /* Ende Generierung der Tabelle */

  /* Neuberechnung der Kapazitäten bei Eingabe einer neuen VM */
  useEffect(() => {
    if (props.capacityDetail) calculateCapacities(true);
  }, [props.capacityVMs]);

  /* Aufruf der Funktion zur Aktualisierung der gespeicherten VMs */
  const updateVMs = (event) => {
    if (event === "delete") {
      props.callbackVMs(event);
    } else {
      event.preventDefault();
      /* Erstellung eines Objektes zur Übergabe an Funktion in Form Komponente */
      let value = {
        memory: parseFloat(event.target.detailedMemory.value),
        storage: parseFloat(event.target.detailedStorage.value),
        amount: parseFloat(event.target.detailedAmount.value),
      };
      /* Löschen der Eingaben in den Textfeldern */
      event.target.detailedMemory.value = "";
      event.target.detailedStorage.value = "";
      event.target.detailedAmount.value = "";
      props.callbackVMs(value);
    }
  };

  /* Detaileingabe aktivieren/deaktivieren */
  const toggleCapacityDetail = (event) => {
    props.callbackCapacityDetail();
    if (event.target.checked) {
      calculateCapacities(
        true
      ); /* Bei Aktivierung: Berechnung der Kapazitäten aus Werten der eingegebenen VMs */
    } else {
      props.callbackCapacities([0, 0, 0, props.capacities[3]]); /* Bei Deaktivierung: Setzen der Kapazitätswerte auf 0, um Werte direkt ausfüllen zu können */
    }
  };

  /* Berechnung der Kapazitäten aus Werten der eingegebenen VMs */
  const calculateCapacities = (toggle) => {
    if (toggle) {
      let sumMemory = 0;
      let sumStorage = 0;
      let sumAmount = 0;

      /* Für jede VM: Erhöhung der Gesamtwerte mit Einzelwerten * Anzahl */
      for (let i = 0; i < props.capacityVMs.length; i++) {
        const element = props.capacityVMs[i];
        sumMemory += element["memory"] * element["amount"];
        sumStorage += element["storage"] * element["amount"];
        sumAmount += element["amount"];
      }
      /* durchschnittliche Kapazitäten = Gesamtwerte durch die Gesamtanzahl der VMs */
      const capacities = [
        (sumMemory / sumAmount).toFixed(2),
        (sumStorage / sumAmount).toFixed(2),
        sumAmount,
        props.capacities[3],
      ];
      props.callbackCapacities(capacities);
    }
  };

  return (
    <div className="inputCapacity">
      <CapacityCards
        capacities={props.capacities}
        callbackCapacities={props.callbackCapacities}
        growthRate={props.growthRate}
        callbackgrowth={props.callbackgrowth}
      />

      {/* Checkbox zur Aktivierung der Detaileingabe */}
      <div className="toggleContainer">
        <input
          type="checkbox"
          className="toggle"
          id="detailToggle"
          checked={props.capacityDetail}
          onChange={(e) => toggleCapacityDetail(e)}
        />
        <label
          htmlFor="detailToggle"
          data-checked="Detail Eingabe"
          data-unchecked="Detail Eingabe"
        ></label>
      </div>
      {/* Form zur Eingabe der einzelnen VMs und Tabelle zur Darstellung der Eingaben */}
      <div className="capacityInputDetail">
        <form
          className="capacityInputDetailedForm"
          onSubmit={(e) => updateVMs(e)}
        >
          <div className="capacityInputDetailedCard">
            <label
              className={props.capacityDetail ? "" : "disabled"}
              htmlFor="detailedMemory"
            >
              Arbeitsspeicher der VM (in GB):
            </label>
            <input
              className="inputCapacityDetailed"
              disabled={props.capacityDetail ? false : true}
              id="detailedMemory"
              name="detailedMemory"
              autoComplete="off"
              required
            />
          </div>
          {/*<div className="capacityInputDetailedCard">
            <label
              className={props.capacityDetail ? "" : "disabled"}
              htmlFor="detailedCPU"
            >
              Anzahl vCPUs der VM:
            </label>
            <input
              className="inputCapacityDetailed"
              disabled={props.capacityDetail ? false : true}
              id="detailedCPU"
              name="detailedCPU"
              autoComplete="off"
              required
            />
          </div>*/}
          <div className="capacityInputDetailedCard">
            <label
              className={props.capacityDetail ? "" : "disabled"}
              htmlFor="detailedStorage"
            >
              Speicherplatz der VM (in GB):
            </label>
            <input
              className="inputCapacityDetailed"
              disabled={props.capacityDetail ? false : true}
              id="detailedStorage"
              name="detailedStorage"
              autoComplete="off"
              required
            />
          </div>
          <div className="capacityInputDetailedCard">
            <label
              className={props.capacityDetail ? "" : "disabled"}
              htmlFor="detailedAmount"
            >
              Anzahl dieser VMs:
            </label>
            <input
              className="inputCapacityDetailed"
              disabled={props.capacityDetail ? false : true}
              id="detailedAmount"
              name="detailedAmount"
              autoComplete="off"
              required
            />
          </div>
          <button
            className={props.capacityDetail ? "" : "disabled"}
            disabled={props.capacityDetail ? false : true}
            id="capacityInputDetailedSubmit"
            type="submit"
          >
            Hinzufügen
          </button>
          <button
            className={props.capacityDetail ? "" : "disabled"}
            disabled={props.capacityDetail ? false : true}
            id="capacityInputDetailedDelete"
            type="button"
            onClick={() => updateVMs("delete")}
          >
            Entfernen
          </button>
        </form>
        <div
          className={props.capacityDetail ? "" : "disabled"}
          id="capacityTable"
        >
          {detailedTable}
        </div>
      </div>
      <button
        className="continue inputCapacity"
        id="btnToResult"
        onClick={() => props.callbackTab(3)}
      >
        Ergebnis
      </button>
    </div>
  );
}

export default InputCapacity;